export default {
	name: 'tuiTag',
	props: {
		type: {
			type: String,
			default: 'primary'
		},
		//padding
		padding: {
			type: String,
			default: '16rpx 26rpx'
		},
		margin: {
			type: String,
			default: '0'
		},
		//文字大小 rpx
		size: {
			type: String,
			default: '28rpx'
		},
		// circle, square，circleLeft，circleRight
		shape: {
			type: String,
			default: 'square'
		},
		//是否空心
		plain: {
			type: Boolean,
			default: false
		},
		//点击效果
		hover: {
			type: Boolean,
			default: false
		},
		//缩放倍数
		scaleMultiple: {
			type: Number,
			default: 1
		},
		originLeft: {
			type: Boolean,
			default: false
		},
		originRight: {
			type: Boolean,
			default: false
		},
		index: {
			type: Number,
			default: 0
		}
	},
	methods: {
		handleClick() {
			this.$emit('click', { index: this.index });
		},
		getTypeClass: function(type, plain) {
			return plain ? 'tui-' + type + '-outline' : 'tui-' + type;
		},
		getClassName: function(shape, plain) {
			//circle, square，circleLeft，circleRight
			var className = plain ? 'tui-tag-outline ' : '';
			if (shape != 'square') {
				if (shape == 'circle') {
					className = className + (plain ? 'tui-tag-outline-fillet' : 'tui-tag-fillet');
				} else if (shape == 'circleLeft') {
					className = className + 'tui-tag-fillet-left';
				} else if (shape == 'circleRight') {
					className = className + 'tui-tag-fillet-right';
				}
			}
			return className;
		}
	}
};
